import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import Loading from 'UNIT_PATH/loading';
var Model = require('UNIT_PATH/model');
var View = require('UNIT_PATH/view');

var main  = () => {
	// 点击(设置/取消)开售提醒
	$('.btn-start-remind').bind('tap', function() {
		var _this = this;

		Loading.showLoading({
	        type: 'load-fullscreen',
	        loadingTxt: '数据获取中..'
	    }, function(_loadingHtml) {
	        $('.red-page').append(_loadingHtml);
	    });

		Model.notifySwitchRequest($(_this), function(data) {
			Loading.hideLoading('load-fullscreen');
			View.renderNotifyDetailResult($(_this), data);
		});
	});
}

export default main;


