import $               from 'ZEPTO_PATH';
import VARIABLE        from 'UNIT_PATH/variable';
import header          from 'MODULES_PATH/header.js';
import layout          from 'MODULES_PATH/layout';
import redSlider       from 'MODULES_PATH/redSlider';
import FooterAd        from 'MODULES_PATH/footerAd';
import goTop           from 'MODULES_PATH/goTop';
import LazyLoad        from 'MODULES_PATH/lazyLoad';
import notifySortSlider from 'MODULES_PATH/notifySortSlider';
import notifyGdsList   from 'MODULES_PATH/notifyGdsList';
import notifySort      from 'MODULES_PATH/notifyGdSort';
import lazySuccess     from 'MODULES_PATH/lazySuccess';
import filterFloat     from 'MODULES_PATH/newUserFilterFloat';
import Tick            from 'UNIT_PATH/Tick';
import notifyDetailSwitch from 'MODULES_PATH/notifyDetailSwitch';
import flexibleArea     from 'MODULES_PATH/flexibleArea';

//VARIABLE.CONFIG.ANIMA_SCROLL = true;
window.$ = $;

// 页面布局调整
layout();

// 回到顶部按钮
goTop();

// 懒加载
LazyLoad({
	loadSucess: function(img) {
		lazySuccess.loadLogoImg(img, '.gds-wrap');
	}
});

// 公共头部
header.headerInit();

// 尾部广告
var footerAd = new FooterAd();

footerAd.init();

// 排序滑动 
notifySortSlider();

// 倒计时
var timerBox = $('#timer-box');
var day = timerBox.find('.day');
var hour = timerBox.find('.hour');
var minute = timerBox.find('.minute');
var second = timerBox.find('.second');
var em = timerBox.find('em').eq(0);
var emSec = timerBox.find('em').eq(2);
var tick = new Tick({
	disTime: timerBox.attr('data-time'),
	numberDouble: true,
	run: (time) => {
		if(time.day != '00') {
			day.removeClass('none');
			em.removeClass('none');
			day.text(time.day);
			second.addClass('none');
			emSec.addClass('none')
		}else{
			day.addClass('none');
			em.addClass('none');
			second.removeClass('none');
			emSec.removeClass('none')
		}

		hour.text(time.hour);
		minute.text(time.min);
		second.text(time.sec);
	}
});

tick._run();

// 自定义区域缩放
flexibleArea($('.flexible-area:not(.none) .flexible-area-inner'), function(_this) {
	_this.removeClass('op0').removeClass('none');
	_this.closest('.flexible-area-con').find('.load-wrap').remove();
	VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
});

// 翻页
notifyGdsList();

// 排序
notifySort();

// 开售提醒
notifyDetailSwitch();

if($('body').attr('data-navi-home') == '1') {
	$('#back-to-home').removeClass('none');
	if($('body').attr('data-vs') == 'jdapp') {
		$('#back-to-home').attr('href', '//red.m.jd.com/app/index.html');
	}else if($('body').attr('data-vs') == 'weixin') {
		$('#back-to-home').attr('href', '//red.m.jd.com/wq/index.html');
	}else{
		$('#back-to-home').attr('href', '//red.m.jd.com/m/index.html');
	}
}



