import $ from 'ZEPTO_PATH';
import Swiper from 'SWIPER_JS_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import updateSlider from 'UNIT_PATH/updateSlider';
import Loading from 'UNIT_PATH/loading';

import model from 'UNIT_PATH/model';
import view from 'UNIT_PATH/view';

var body_dom = $('body');

function main() {
	var navSwiper = new Swiper('.notify-filter-slider .swiper-container',{
		slidesPerView : 'auto',
		loop: false,
		preventClicks : true,
		freeMode: true,
		freeModeSticky: true,
		onClick: function(swiper) {
			tabClick(swiper);
		},
		onInit: function(swiper) {

		}
	});

	VARIABLE.GLOBAL.NOTIFY_DETAIL.navSwiper = navSwiper;

	// 浏览器端旋转设备时刷新尺寸
	if(VARIABLE.CONFIG.VS != 'jdapp') {
		$(window).bind('orientationchange', function() {
			var active_slider = $('.swiper-slide.active', navSwiper).get(0);

			if(active_slider) {
				updateSlider(navSwiper);
				slide_to(navSwiper, active_slider, navSwiper, 0);
			}			
		});
	}
}

function tabClick(swiper) {
	var clickedIndex = swiper.clickedIndex;
	var go_top_btn = $('#go-top');
	var gds = $('.notifygds-list');
	var ul = gds.find('ul');
	var typeFirst = $('.type-group').eq(0);
	var typeSize  = $('.type-group').eq(1);
	var typeColor = $('.type-group').eq(2);

	if(typeof clickedIndex == 'undefined') return;

	swiper.slides.removeClass('active');
	$(swiper.clickedSlide).addClass('active');

	var attrId = $(swiper.clickedSlide).attr('data-type-id');
	
	typeFirst.find('li').removeClass('selected');
	typeFirst.find('li[data-type-id="'+ attrId +'"]').addClass('selected');

	typeSize.find('li').removeClass('selected').eq(0).addClass('selected');
	typeColor.find('li').removeClass('selected').eq(0).addClass('selected');

	if(typeFirst.find('li.all').hasClass('selected')) {
		$('.filter').removeClass('active');
	}else{
		getColorSize();
		$('.filter').addClass('active');
	}

	var active_slider     = swiper.clickedSlide;
	var swiper_width_half = parseInt(swiper.width / 2, 10);
	var offset_left       = active_slider.offsetLeft;
	var max_translate     = swiper.maxTranslate();
	var min_translate     = swiper.minTranslate();
	var translate_to      = swiper_width_half - offset_left - active_slider.offsetWidth / 2;

	if(translate_to > min_translate) {
		translate_to = min_translate;
	}else if(translate_to < max_translate) {
		translate_to = max_translate;
	}
	swiper.setWrapperTransition(500);
	swiper.setWrapperTranslate(translate_to);
	body_dom.attr('data-tab', swiper.clickedIndex);

	VARIABLE.GLOBAL.NOTIFY_DETAIL.getMoreLoading = true;
	body_dom.attr('data-page', '0');

	Loading.showLoading({
        type: 'load-fullscreen',
        loadingTxt: '数据获取中..'
    }, function(_loadingHtml) {
        $('.red-page').append(_loadingHtml);
    });

	// 请求商品信息
	model.notifyGoodsList(function(data) {
		Loading.hideLoading('load-fullscreen');
		VARIABLE.GLOBAL.NOTIFY_DETAIL.getMoreLoading = false;
		go_top_btn.addClass('none');
		ul.html(data);
		if(!ul.find('li').length) {
			$('.no-goods').removeClass('none');
		}else{
			$('.no-goods').addClass('none');
		}
		body_dom.attr('data-page', '1');
		body_dom.attr('data-total-page', $('.skus').attr('data-total-page'));
		VARIABLE.GLOBAL.LAZY.refreshImg();
		$(window).scrollTop(0);
		getPriceAndStock(1);
	});
}

// 获取颜色和尺码
function getColorSize(callback) {
	model.getColorAndSizeData(function(data) {
		view.renderSizeColor(data);
		VARIABLE.GLOBAL.NOTIFY_DETAIL.modal_scroller && VARIABLE.GLOBAL.NOTIFY_DETAIL.modal_scroller.refresh();
		callback && callback();
	});
}

function getPriceAndStock(page) {
	var skus = $('.skus').eq(page - 1).attr('data-skus');

	if(!skus) return;

	model.getPriceData(skus, function(data) {
		var rule = $('body').attr('data-promotion-string');

		if(rule) {
			view.renderNotifyPrice(skus, data);
		}else{
			view.renderNotifyPriceNormal(skus.split(','), data);
		}
	});
}

function slide_to(swiper, active_slider, swiper_box, time) {
	var li_fix            = $('.nav-fix-item');
	var swiper_width_half = parseInt(swiper_box.width() / 2, 10);
	var offset_left       = active_slider.offsetLeft + li_fix.width();
	var max_translate     = swiper.maxTranslate();
	var min_translate     = swiper.minTranslate();
	var translate_to      = swiper_width_half - offset_left - active_slider.offsetWidth / 2;

	if(translate_to > min_translate) {
		translate_to = min_translate;
	}else if(translate_to < max_translate) {
		translate_to = max_translate;
	}
	swiper.setWrapperTransition(time);
	swiper.setWrapperTranslate(translate_to);
}

module.exports = main;


