import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
var model = require('UNIT_PATH/model');
var view = require('UNIT_PATH/view');

var gds = $('.notifygds-list');
var goods_items = $('.notifygds-list a');
var ul = gds.find('ul');
var fisrt_item = goods_items.eq(0);
var $win = $(window);
var body_dom = $('body');
var winHeight = $win.height();

function main() {
	if(!ul.find('li').length) {
		return $('.no-goods').removeClass('none');
	}

	// 获取价格和库存
	getPriceAndStock(1);

	// 列表滚动翻页
	// $(window).bind('scroll', function() {
	// 	scroll();
	// });
}

function scroll() {
	if(fisrt_item.length) {
		var scrollTop = $win.scrollTop();
		var gdsHeight = gds.height();
	    var footerLine = gds.offset().top + gdsHeight;

	    if(winHeight + scrollTop >= footerLine) {
	    	jumpPage();
	    }
	}else{
		jumpPage();
	}

	function jumpPage() {
		if(VARIABLE.GLOBAL.NOTIFY_DETAIL.getMoreLoading) return;
		VARIABLE.GLOBAL.NOTIFY_DETAIL.getMoreLoading = true;

		var page_now   = parseInt(body_dom.attr('data-page'), 10) + 1;
		var page_total = parseInt(body_dom.attr('data-total-page'), 10);

		// 判断是不是已经到了最后一页
		if(page_now > page_total)return;

		model.notifyGoodsList(function(data) {
			ul.append(data);
			VARIABLE.GLOBAL.LAZY.refreshImg();
			VARIABLE.GLOBAL.NOTIFY_DETAIL.getMoreLoading = false;
			getPriceAndStock(page_now);
			body_dom.attr('data-page', page_now);
		});
	}
}

function getPriceAndStock(page) {
	var skus = $('.skus').eq(page - 1).attr('data-skus');

	if(!skus) return;

	if($('body').attr('data-totoal-price-promotion') == '1') {
		$('#notifygds-list .old').remove();
	}

	model.getPriceData(skus, function(data) {
		var rule = $('body').attr('data-promotion-string');

		if($('body').attr('data-totoal-price-promotion') == '1'){
			return view.renderNotifyPrice(skus, data, false);
		}

		if(rule) {
			view.renderNotifyPrice(skus, data, true);
		}else{
			view.renderNotifyPriceNormal(skus.split(','), data);
		}
	});
}

export default main;


